import React from 'react';
import PropTypes from "prop-types";
import {NavLink} from "react-router-dom";
import globalVariables from "../other/GlobalVariables";
import "../../css/App.css"

const UnderConstruction = (props) => {

    return (
        <div className={"mainContents "+globalVariables.colorMode+" mainApp"}
             style={{alignItems:"center", justifyContent:"center", flexDirection:"column"}}>
            <span style={{fontSize:32}}>WARNING</span>
            <svg xmlns="http://www.w3.org/2000/svg" width="150px" height="150px"  className={globalVariables.colorMode+" svgDefault"}
                 viewBox="0 0 16 16">
                <path d="M8.982 1.566a1.13 1.13 0 0 0-1.96 0L.165 13.233c-.457.778.091 1.767.98 1.767h13.713c.889 0 1.438-.99.98-1.767L8.982 1.566zM8 5c.535 0 .954.462.9.995l-.35 3.507a.552.552 0 0 1-1.1 0L7.1 5.995A.905.905 0 0 1 8 5zm.002 6a1 1 0 1 1 0 2 1 1 0 0 1 0-2z"/>
            </svg>
            <span style={{fontSize:18}}>{props.tabName} not ready for access</span>
            <NavLink to={globalVariables.rootDir+"/"+props.tabName+"/beta"}>
                View Anyways
            </NavLink>
        </div>
    );
};
UnderConstruction.propTypes = {
    tabName:PropTypes.string
};
UnderConstruction.defaultProps= {
    tabName:"void"
}
export default UnderConstruction;
